// doc https://cn.vuejs.org/v2/guide/custom-directive.html
import Vue from 'vue'
Vue.directive('preventReClick', {
    inserted(el, binding) {
        console.log("preventReClick")
      el.addEventListener('click', () => {
        if (!el.disabled) {
          el.disabled = true
  
          setTimeout(() => {
            el.disabled = false
  
          }, binding.value || 3000)
  
        }
  
      })
    }
})


