import _import from '../utils/_import'
// 首页
export default [{
  path: '/',
  component: _import('layout/index'),
  children: [{
    path: '/enter',
    component: _import('enter'),
    children: [{
        path: 'entry',
        name: 'enterForm',
        component: _import('enter/entry'),
        meta: {
          title: '考试报名',
          NavBarOption: {
            leftText: '报名须知',
            title: '考试报名',
          },
        }
      },
      {
        path: 'notice',
        name: 'enterNotice',
        component: _import('enter/notice'),
        meta: {
          title: '报名须知',
          NavBarOption: {
            title: '报名须知'
          },
        }
      }
    ]
  }]
}]



