export default {
    state: {
        USERINFO: {}, // 用户信息
    },

    mutations: {
        SET_USERINFO(state, options) {
            state.USERINFO = options
        },
    },

    actions: {
        setUSERINFO(context, options) {
            context.commit('SET_USERINFO', options)
        },
    },

    getters: {
        getUSERINFO(state) {
            return state.USERINFO
        }
    }

}


