import {loadRestModules} from '@/utils/loadModules'
let routeModules = require.context('./modules', false, /\.js$/)
let commonModules = require.context('../modules', false, /\.js$/)
const routeEnum = Object.values(loadRestModules(routeModules))
const commonEnum = Object.values(loadRestModules(commonModules))

export default [
  {
    path: '*',
    redirect: '404/entry'
  },
  {
    path: '/',
    redirect: 'list/entry'
  }
].concat(...routeEnum, ...commonEnum)



