import moment from 'moment';

//截取日期时间
export function shortTime (value){
  if(!value) return ""
  let changeTime = moment(value).format('YYYY-MM-DD')
  if (changeTime=='Invalid date') return ""
  return changeTime
}
/**
 * 数字格式化
 *
 * @export
 * @param {any} number 要格式化的数字
 * @param {any} decimals 保留几位小数
 * @param {any} decPoint 小数点符号
 * @param {any} thousandsSep 千分位符号
 * @returns
 */
export function numberFormat (number, decimals, decPoint, thousandsSep) {
  number = (number + '').replace(/[^0-9+-Ee.]/g, '')
  let n = !isFinite(+number) ? 0 : +number
  let prec = !isFinite(+decimals) ? 0 : Math.abs(decimals)
  let sep = typeof thousandsSep === 'undefined' ? ',' : thousandsSep
  let dec = typeof decPoint === 'undefined' ? '.' : decPoint
  let s = ''
  let toFixedFix = function (n, prec) {
    let k = Math.pow(10, prec)
    return '' + Math.ceil(n * k) / k
  }

  s = (prec ? toFixedFix(n, prec) : '' + Math.round(n)).split('.')
  let re = /(-?\d+)(\d{3})/
  while (re.test(s[0])) {
    s[0] = s[0].replace(re, '$1' + sep + '$2')
  }

  if ((s[1] || '').length < prec) {
    s[1] = s[1] || ''
    s[1] += new Array(prec - s[1].length + 1).join('0')
  }
  return s.join(dec)
}

/**
 *时间格式化  @overrite date
 * 
 * @export  
 * @param  {any} record 时间戳&标准时间格式
 * @return {void} 
 */
export function timeFormat(record){
  let timer = new Date(record)
  //@Override
  Date.prototype.format = function (format) {
    var o = {
      "M+": timer.getMonth() + 1,
      "d+": timer.getDate(),
      "h+": timer.getHours(),
      "m+": timer.getMinutes(),
      "s+": timer.getSeconds(),
      "q+": Math.floor((timer.getMonth() + 3) / 3),
      S: timer.getMilliseconds(),
    };
    if (/(y+)/.test(format)) {
      format = format.replace(
        RegExp.$1,
        (timer.getFullYear() + "").substr(4 - RegExp.$1.length)
      );
    }
    for (var k in o) {
      if (new RegExp("(" + k + ")").test(format)) {
        format = format.replace(
          RegExp.$1,
          RegExp.$1.length == 1
            ? o[k]
            : ("00" + o[k]).substr(("" + o[k]).length)
        );
      }
    }
    return format;
  };
}




