<template>
  <div id="AdditionApp">
    <transition name="slide">
      <div class="app-container h100">
        <div class="m-h100" :class='`theme-${theme}`' v-if="isOpen">
          <router-view />
        </div>
      </div>
    </transition>
  </div>
</template>
<script>

export default {
  name: "AdditionApp",
  data() {
    return {
      theme: "daily", // daily dark
      isOpen: true, // todo
    };
  },

  mounted() {
  
  },

  methods: {
  
  },
};
</script>
<style lang="scss" scoped>
@import "@/assets/css/theme.scss";
#AdditionApp {
  height: 100%;
  margin: auto;
}
</style>


