import _import from '../../utils/_import'
import _importAddition from '../../utils/_importAddition'

// 首页
export default [{
  path: '/',
  component: _import('layout/index'),
  children: [{
    path: '/list',
    component: _importAddition('list'),
    children: [{
        path: 'entry',
        name: 'listForm',
        component: _importAddition('list/entry'),
        meta: {
          title: '教材列表',
          NavBarOption: {
            leftText: '返回',
            title: '教材列表',
          },
        }
      },
    ]
  }]
}]



