/**
 * 
 * @param {*} record 活动状态
 */
export function activityCardName(record) {
  if (record == 1) {
    return "进行中"
  } else if (record == "进行中") {
    return "active"
  } else {
    return "start"
  }
}

/**
 * 
 * @param {*} type 广告位置
 */
export function formatadType(type) {
  switch (type) {
    case "kczDetail_Top": // 看常州文稿详情顶部
      return "mwxJTW"
    case "kczDetail_Bottom": // 看常州文稿详情底部
      return "qFOTY5"
    case "mineAsk_Top": // 问政提问详情顶部
      return "fzBQ29"
    case "mineAsk_Bottom": //问政提问详情底部
      return "xFKLS2"
    case "czhDetail_Top": // 常州号文稿详情顶部
      return "cnpHS9"
    case "czhDetail_Bottom": // 常州号文稿详情底部
      return "dmvCFU"
  }
}

/**
 * 
 * @param {*} level 等级分值
 */
export function formatLevel(level) {
  switch (level) {
    case 1:
      return 10
    case 2:
      return 120
    case 3:
      return 200
    case 4:
      return 400
    case 5:
      return 800
    case 6:
      return 1200
    case 7:
      return 1600
    case 8:
      return 1800
    case 9:
      return 2200
    case 10:
      return 2400
    case 11:
      return 3200
    case 12:
      return 3400
    case 13:
      return 3800
    case 14:
      return 4000
    case 15:
      return 4400
    case 16:
      return 4800
  }
}



